const API_KEY = "756632b0cb6004a03d07537676dccfbf";
const BASE_PATH = "https://api.themoviedb.org/3";

export interface IMovie {
  id: number;
  backdrop_path: string;
  poster_path: string;
  title: string;
  overview: string;
  name: string;
}

export interface IMoviesResult {
  dates: {
    maximum: string;
    minimum: string;
  };
  page: number;
  results: IMovie[];
  total_pages: number;
  total_results: number;
}

export function getMovies(category: string) {
  return fetch(`${BASE_PATH}/movie/${category}?api_key=${API_KEY}`).then(
    (response) => response.json()
  );
}

export function getTvs(category: string) {
  return fetch(`${BASE_PATH}/tv/${category}?api_key=${API_KEY}`).then(
    (response) => response.json()
  );
}

export interface IMovieDetail {
  id: number;
  genres: { name: string }[];
  runtime: number | null;
  release_date: string;
  first_air_date: string;
  last_air_date: string;
  episode_run_time: [];
  number_of_seasons: number;
  backdrop_path: string;
  title: string;
  name: string;
}

export function getMovieDetail(content: string, id: string) {
  return fetch(`${BASE_PATH}/${content}/${id}?api_key=${API_KEY}`).then(
    (response) => response.json()
  );
}

export interface IMovieCast {
  cast: { name: string }[];
}
export function getMovieCast(content: string, id: string) {
  return fetch(`${BASE_PATH}/${content}/${id}/credits?api_key=${API_KEY}`).then(
    (response) => response.json()
  );
}

interface ISearchResult {
  id: number;
  media_type: string;
  title: string;
  backdrop_path: string;
  overview: string;
  name: string;
}
export interface ISearch {
  results: ISearchResult[];
}

export function getSearchMulti(keyword: string) {
  return fetch(
    `${BASE_PATH}/search/multi?api_key=${API_KEY}&query=${keyword}&include_adult=false`
  ).then((response) => response.json());
}
